import { useState, useRef } from "react";
import convertBase64 from "@/utils/convertBase64";
import { uploadAvatar } from "@/services/firebase";
import useUserStore from "@/store/users";
import styles from './SideBar.module.scss';

export default function Sidebar() {
  const { user, setUser } = useUserStore();
  const fileInputRef = useRef<HTMLInputElement | null>(null);
  const [previewUrl, setPreviewUrl] = useState<string | null>(user?.avatarUrl || null);
  const [uploading, setUploading] = useState(false);

  const handleImageChange = async (e: React.ChangeEvent<HTMLInputElement>) => {
    if (!e.target.files || !user) return;

    const file = e.target.files[0];

    // Base64 formatında anlık önizleme
    const base64 = await convertBase64(file);
    console.log("Base64 Resim:", base64); // Base64 verisini kontrol et
    setPreviewUrl(base64); // Base64 verisini anında göster

    setUploading(true);
    try {
      const avatarUrl = await uploadAvatar(file, user.uid); // Firebase'e yükle
      const updatedUser = { ...user, avatarUrl };
      setUser(updatedUser);
      localStorage.setItem("user", JSON.stringify(updatedUser));
    } catch (err) {
      console.error("Avatar yükleme hatası:", err);
    } finally {
      setUploading(false);
    }
  };

  return (
    <div className={styles.sidebar}>
      <div className={styles.avatarContainer}>
        <input
          type="file"
          accept="image/*"
          onChange={handleImageChange}
          ref={fileInputRef}
          style={{ display: 'none' }}
        />
        <div
          className={styles.avatarImage}
          onClick={() => fileInputRef.current?.click()}
        >
          {previewUrl ? (
            <img src={previewUrl} alt="Avatar" className={styles.avatarPreview} />
          ) : (
            <div className={styles.avatarPlaceholder}>
              <p>Tıklayın</p>
            </div>
          )}

          {uploading && (
            <div className={styles.uploadingOverlay}>
              Yükleniyor...
            </div>
          )}
        </div>
        <p className={styles.avatarText}>
          {user?.email}
        </p>
      </div>
    </div>
  );
}
